import React, { cloneElement } from 'react';
import styled from 'styled-components';
import { COLORS } from '../../common/consts/colors';

const RED_DOT_CSS = `
  display: inline-block;
  box-sizing: border-box;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: #FF5219;
  border: 2px solid white;
  vertical-align: top;
  top: 0px;
  right: 0px;
`;

export const Dot = styled.span`
  display: inline-block;
  ${RED_DOT_CSS}
`;

const ClassWrapper = ({ children, className }: { children: React.ReactElement; className?: string }) => {
  return className ? cloneElement(children, { className: `${className} ${children.props.className || ''}` }) : children;
};
export const DotWrapper = styled(ClassWrapper)`
  &::after {
    content: '';
    ${RED_DOT_CSS}
  }
`;


const BADGE_TEXT_CSS = `
  display: inline-block;
  font-size: 12px;
  line-height: 12px;
  text-align: center;
  height: 12px;
  border-radius: 2px;
  margin-left: 8px;
  padding: 0 2px;
  span {
    display: inline-block;
    transform: scale(0.83);
  }
`;


export const New = styled.div`
  color: rgb(255, 146, 0);
  background-color: rgba(255, 146, 0, 0.12);
  ${BADGE_TEXT_CSS}
`;


export const Beta = styled.div`
  color: ${COLORS.COMMON.BLUE1_HOVER_COLOR};
  background-color: rgba(56, 155, 255, 0.12);
  ${BADGE_TEXT_CSS}
`;
